
.wrap {
  min-height: 100%;
  position: relative;
}
section.article {
  line-height: 1.6;
  padding-bottom: 160px;
}
section.article article .title {
    margin: 0.65em 0;
    font-size: 1.5em;
}
section.article article .info {
    color: #7f8c8d;
    margin: 1.2em 0;
}
section.article article .info span {
      margin-left: 0.5rem;
}
section.article article .content h2, section.article article .content h3, section.article article .content h4, section.article article .content h5, section.article article .content h6 {
    position: relative;
    margin: 1em 0;
}
section.article article .content h2:before, section.article article .content h3:before, section.article article .content h4:before, section.article article .content h5:before, section.article article .content h6:before {
      content: "#";
      color: #42b983;
      position: absolute;
      left: -0.7em;
      top: -4px;
      font-size: 1.2em;
      font-weight: bold;
}
section.article article .content h4:before, section.article article .content h5:before, section.article article .content h6:before {
    content: "";
}
section.article article .content h2, section.article article .content h3 {
    font-size: 22px;
}
section.article article .content h4, section.article article .content h5, section.article article .content h6 {
    font-size: 18px;
}
section.article article .content a {
    color: #42b983;
    word-break: break-all;
}
section.article article .content blockquote {
    margin: 2em 0;
    padding-left: 20px;
    border-left: 4px solid #42b983;
}
section.article article .content img {
    display: block;
    max-width: 100%;
    margin: 1em auto;
}
section.article article .content code,
  section.article article .content pre {
    font-size: 0.8em;
    background-color: #f8f8f8;
    font-family: 'Roboto Mono', Monaco, courier, monospace;
    color: #525252;
}



