
section.editor {
  height: 250%;
  position: absolute;
  left: 15%;
  width: 60%;
  height: 90%;
}
section.editor .title {
    text-align: center;
    box-sizing: border-box;
    padding: 10px;
    margin: 0 auto;
    height: 50px;
    display: block;
    font-size: 30px;
    color: #2c3e50;
}
section.editor textarea, section.editor button, section.editor article {
    box-sizing: border-box;
    border-radius: 6px;
    height: 100%;
    float: left;
    transition: width 0.6s;
    font-size: 16px;
    line-height: 1.8;
    border: 1px solid #42b983;
}
section.editor textarea, section.editor article {
    resize: none;
    padding: 20px;
    overflow-y: auto;
}
section.editor button {
    border: 1px solid #42b983;
    color: #42b983;
    background-color: #FFF;
    transition: all 0.6s;
    border-radius: 4px;
    margin: 0 10px;
    width: 20px;
    text-align: center;
}
section.editor button:hover {
      background-color: #42b983;
      color: #FFF;
}
section.editor .edit, section.editor .inspect {
    height: 80%;
    margin: 0 50px;
    overflow: hidden;
}
section.editor .edit textarea {
    width: calc(100% - 40px);
}
section.editor .edit article {
    width: 0;
}
section.editor .inspect textarea {
    width: calc(50% - 20px);
}
section.editor .inspect article {
    width: calc(50% - 20px);
}
section.editor .panel {
    margin: 20px 50px;
    position: relative;
    height: 30px;
    line-height: 30px;
}
section.editor .panel input {
      text-align: center;
      border-bottom: 1px solid #42b983;
}
section.editor .panel button.saveArticle {
      border: 1px solid #42b983;
      color: #42b983;
      background-color: #FFF;
      transition: all 0.6s;
      border-radius: 4px;
      float: right;
      height: 30px;
      width: 80px;
}
section.editor .panel button.saveArticle:hover {
        background-color: #42b983;
        color: #FFF;
}
section.editor article {
    line-height: 1.6;
    padding-bottom: 160px;
}
section.editor article h2, section.editor article h3, section.editor article h4, section.editor article h5, section.editor article h6 {
      position: relative;
      margin: 1em 0;
}
section.editor article h2:before, section.editor article h3:before, section.editor article h4:before, section.editor article h5:before, section.editor article h6:before {
        content: "#";
        color: #42b983;
        position: absolute;
        left: -0.7em;
        top: -4px;
        font-size: 1.2em;
        font-weight: bold;
}
section.editor article h4:before, section.editor article h5:before, section.editor article h6:before {
      content: "";
}
section.editor article h2, section.editor article h3 {
      font-size: 22px;
}
section.editor article h4, section.editor article h5, section.editor article h6 {
      font-size: 18px;
}
section.editor article a {
      color: #42b983;
      word-break: break-all;
}
section.editor article blockquote {
      margin: 2em 0;
      padding-left: 20px;
      border-left: 4px solid #42b983;
}
section.editor article img {
      display: block;
      max-width: 100%;
      margin: 1em auto;
}
section.editor article code,
    section.editor article pre {
      font-size: 0.8em;
      background-color: #f8f8f8;
      font-family: 'Roboto Mono', Monaco, courier, monospace;
      color: #525252;
}
@media screen and (max-width: 500px) {
section.editor {
      position: absolute;
      width: 70%;
      height: 40%;
      left: 15%;
}
section.editor .title {
        width: 50%;
        left: 25%;
}
}



