
div #setup {
    position:absolute;
    //left:0%;
    //top:20%;
	width:100%;
	height:80%;
}
.form {
      //width: 500px;
      width:100%;
      height: 400px;
      //width:45%;
      //height:45%;
      //margin: 0 auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
}
.icon i {
      transition: all 4s;
}
.icon i:hover {
      transform: rotate(1440deg);
}
.info {
      margin: 10px;
      font-size: 12px;
      height: 20px;
}
.input:hover {
      color: #3aa373;
}
p {
      color: #7f8c8d;
      height: 50px;
      text-align: center;
      transition: all 0.4s;
}
p input {
      transition: all 0.4s;
      width: 200px;
      padding: 8px;
      border-bottom: 1px solid #42b983;
}
p input:focus {
      width: 210px;
}
button {
      transition: all 0.4s;
      color: #7f8c8d;
      border: 1px solid #3aa373;
      border-radius: 4px;
      width: 60px;
      height: 30px;
      cursor: pointer;
}
button:hover {
      background-color: #3aa373;
      color: #FFF;
}
@media screen and (max-width: 500px) {
.form {
			position:relative;
			//left:50%;
			//top:40%;
}
}



